/**
 * Test Web Component
 * A simple Web Component to test the Web Component integration
 */

export class TestWebComponent extends HTMLElement {
  private shadow: ShadowRoot;
  private greeting = 'Hello from Web Component!';

  constructor() {
    super();
    this.shadow = this.attachShadow({ mode: 'open' });
    this.render();
    this.setupEventListeners();
  }

  /**
   * Component connected to DOM
   */
  connectedCallback(): void {
    console.log('Test Web Component connected');
  }

  /**
   * Component disconnected from DOM
   */
  disconnectedCallback(): void {
    console.log('Test Web Component disconnected');
  }

  /**
   * Render the component
   */
  private render(): void {
    const styles = this.getStyles();
    const template = this.getTemplate();
    this.shadow.innerHTML = '<style>' + styles + '</style>' + template;
  }

  /**
   * Get component styles
   */
  private getStyles(): string {
    return [
      ':host { display: block; font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif; padding: 5px; }',
      '.test-container { text-align: center; padding: 5px; border: 2px solid #007acc; border-radius: 8px; background: #f8f9fa; }',
      '.greeting { font-size: 24px; color: #007acc; margin-bottom: 16px; }',
      '.description { color: #666; margin-bottom: 5px; }',
      '.button-group { display: flex; gap: 10px; justify-content: center; }',
      '.btn { padding: 8px 16px; border: none; border-radius: 4px; cursor: pointer; font-size: 14px; }',
      '.btn-primary { background: #007acc; color: white; }',
      '.btn-primary:hover { background: #0056b3; }',
      '.btn-secondary { background: #6c757d; color: white; }',
      '.btn-secondary:hover { background: #545b62; }',
      '.counter { font-size: 18px; margin: 10px 0; color: #28a745; }'
    ].join(' ');
  }

  /**
   * Get component template
   */
  private getTemplate(): string {
    return '<div class="test-container">' +
           '<h2 class="greeting">' + this.escapeHtml(this.greeting) + '</h2>' +
           '<p class="description">This is a test Web Component to demonstrate Shadow DOM and native browser APIs.</p>' +
           '<div class="counter" id="counter">Clicks: 0</div>' +
           '<div class="button-group">' +
           '<button class="btn btn-primary" id="greet-btn">Greet</button>' +
           '<button class="btn btn-secondary" id="reset-btn">Reset</button>' +
           '</div>' +
           '</div>';
  }

  /**
   * Setup event listeners
   */
  private setupEventListeners(): void {
    const greetBtn = this.shadow.getElementById('greet-btn') as HTMLButtonElement;
    const resetBtn = this.shadow.getElementById('reset-btn') as HTMLButtonElement;

    if (greetBtn) {
      greetBtn.addEventListener('click', () => this.handleGreet());
    }

    if (resetBtn) {
      resetBtn.addEventListener('click', () => this.handleReset());
    }
  }

  /**
   * Handle greet button click
   */
  private handleGreet(): void {
    const counterEl = this.shadow.getElementById('counter') as HTMLElement;
    if (counterEl) {
      const currentCount = parseInt(counterEl.textContent?.split(': ')[1] || '0');
      counterEl.textContent = 'Clicks: ' + (currentCount + 1);

      // Update greeting
      this.greeting = 'Hello from Web Component! (Clicked ' + (currentCount + 1) + ' times)';
      const greetingEl = this.shadow.querySelector('.greeting') as HTMLElement;
      if (greetingEl) {
        greetingEl.textContent = this.greeting;
      }
    }
  }

  /**
   * Handle reset button click
   */
  private handleReset(): void {
    const counterEl = this.shadow.getElementById('counter') as HTMLElement;
    if (counterEl) {
      counterEl.textContent = 'Clicks: 0';
    }

    this.greeting = 'Hello from Web Component!';
    const greetingEl = this.shadow.querySelector('.greeting') as HTMLElement;
    if (greetingEl) {
      greetingEl.textContent = this.greeting;
    }
  }

  /**
   * Escape HTML for safe rendering
   */
  private escapeHtml(text: string): string {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }
}

// Register the Web Component
customElements.define('vfs-test-component', TestWebComponent);